<template>
    <div class="main-page">
        <div class="newTitle">
            <div class="title">
                <span>{{ dataList.FName }}</span>
            </div>
            <div class="source-wrap">
                <div>
                    <span>发布人:</span><span>{{ dataList.CreateName }}</span>
                </div>

                <div>
                    <span>发布时间:</span><span>{{ $time(dataList.CreateTime).format("YYYY-MM-DD") }}</span>
                </div>
            </div>
            <div>
                <img class="showImg psd" :src="$route.query.src" alt="" style="width:100%;margin-top:10px; display:block">
            </div>
        </div>
        <div class="newContent">
            <span v-html="dataList.FContent"></span>
        </div>

        <h-loading v-if="loading" />
    </div>
</template>

<script>
export default {
    data() {
        return {
            dataList: {},
            loading: true,
        };
    },
    methods: {
        OA_DC_DocumentsDetails() {
            this.loading = true
            this._api.OA_DC_DocumentsDetails({
                Id: this.$route.query.Id,
            }).then((res) => {
                if (res) {
                    this.dataList = res.data;
                }
                this.loading = false
            });
        },
    },
    created() {
        this.OA_DC_DocumentsDetails();
    },
};
</script>

<style lang="scss" scoped>
.main-page {
    padding: 0 15px;
    .scroll-Y {
        height: 100%;
    }

    .content-wrap {
        padding: 30rpx;
    }

    .newTitle {
        .title {
            display: block;
            text-align: center;
            font-weight: 600;
            padding-top: 15px;
            font-size: 18px;
            color: #303133;
            line-height: 40px;
            border-bottom: 1px solid #f2f2f4;
            text-align: center;
        }
    }

    .source-wrap {
        display: flex;
        justify-content: space-between;
        line-height: 82rpx;
        font-size: 13px;
        padding-top: 15px;
        color: #666;
        text {
            font-size: 28rpx;
        }
    }

    .newContent {
        margin-top: 20px;
        margin-bottom: 0px;
        padding: 0px;
        line-height: 42px;
        font-size: 18px;
        color: rgb(51, 51, 51);
        text-align: justify;
        font-family: 微软雅黑;
        white-space: normal;
        background-color: rgb(255, 255, 255);
    }
}
</style>